import React from 'react';
import { Paper, Typography, Grid, Box, Button } from '@mui/material';

function Statistics({ wines, onShowGlobalStats, onShowVeganProducers }) {
 const totalWines = wines.length;
 const veganWines = wines.filter(wine => {
   const label = wine.label.toLowerCase();
   return !label.includes('not vegan') && label.includes('vegan friendly');
 }).length;
 
 const uniqueCountries = [...new Set(wines.map(wine => wine.origin))];
 const currentCountry = uniqueCountries.length === 1 ? uniqueCountries[0] : '';

 return (
   <Paper sx={{ 
     p: 3, 
     mb: 3,
     '& .MuiTypography-h4': {
       color: 'primary.main',
       fontWeight: 500
     }
   }}>
     <Grid container spacing={3} alignItems="center">
       <Grid item xs={12} md={3}>
         <Box sx={{
           p: 2,
           backgroundColor: 'background.default',
           borderRadius: 2
         }}>
           <Typography variant="h6" gutterBottom>Totale vini</Typography>
           <Typography variant="h4">{totalWines}</Typography>
         </Box>
       </Grid>
       <Grid item xs={12} md={3}>
         <Box sx={{
           p: 2,
           backgroundColor: 'background.default',
           borderRadius: 2
         }}>
           <Typography variant="h6" gutterBottom>Vini vegani</Typography>
           <Typography variant="h4">
             {veganWines} ({((veganWines/totalWines)*100).toFixed(1)}%)
           </Typography>
         </Box>
       </Grid>
       {currentCountry && (
         <Grid item xs={12} md={2}>
           <Box sx={{
             p: 2,
             backgroundColor: 'background.default',
             borderRadius: 2
           }}>
             <Typography variant="h6" gutterBottom>Nazione</Typography>
             <Typography variant="h4">{currentCountry}</Typography>
           </Box>
         </Grid>
       )}
       <Grid item xs={12} md={2}>
         <Button 
           variant="outlined" 
           onClick={onShowGlobalStats}
           fullWidth
           sx={{
             height: '100%',
             minHeight: 90,
             borderRadius: 2,
             backgroundColor: 'background.default'
           }}
         >
           Statistiche globali
         </Button>
       </Grid>
       <Grid item xs={12} md={2}>
         <Button 
           variant="outlined" 
           onClick={onShowVeganProducers}
           fullWidth
           sx={{
             height: '100%',
             minHeight: 90,
             borderRadius: 2,
             backgroundColor: 'background.default'
           }}
         >
           Produttori vegani
         </Button>
       </Grid>
     </Grid>
   </Paper>
 );
}

export default Statistics;
